card {
    border-radius: 2px;
    height: 175px;
    margin: 1rem;
    position: relative;
    width: 180px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    background: #E9E9FF;
    border-radius: 6px; 
  }
  
  .img-container {
    text-align: center;
    margin-top: auto;
    margin-bottom: auto;
  }
.wrapper {
    margin-top:10px;
    margin-bottom: 20px; 
    background: rgb(110, 108, 238);
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 20px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
    align-direction: row;
    border-radius: 6px; 
  }
.jumbotron {
    text-align: center;
    margin-bottom: 30px;
    background: rgba(233, 4, 4, 0.904);
    
  }
  
  .navbar {
      border-radius: 6px; 
      margin-bottom: 20px;
  }
footer {
    height: 30px;
    margin-top: 50px;
  }
* {
    box-sizing: border-box;
    font-family: sans-serif;
  }
  
  html, body, #root {
    padding: 0;
    margin: 0;
    height: 100%;
  }
